import React, { Component } from "react";
import classes from "./Quiz.css";
import ActiveQuiz from "../../components/ActiveQuiz/ActiveQuiz";
import FinishedQuiz from "../../components/FinishedQuiz/FinishedQuiz";

class Quiz extends Component {
  state = {
    results: {}, // {[id]: success error}
    isFinished: false,
    activeQuestion: 0,
    answerState: null, // { [id]: 'success' 'error' }
    quiz: [
      {
        question: "How many hours a day do you watch TV?",
        rightAnswerId: 3,
        id: 1,
        answers: [
          { text: "On Tuesday", id: 1 },
          { text: "I watch the news", id: 2 },
          { text: "About two hours", id: 3 },
          { text: "In my living", id: 4 }
        ]
      },
      {
        question: "What will you do this afternoon?",
        rightAnswerId: 1,
        id: 2,
        answers: [
          { text: "I'll play soccer", id: 1 },
          { text: "I was playing soccer", id: 2 },
          { text: "I play soccer", id: 3 },
          { text: "I played soccer", id: 4 }
        ]
      },
      {
        question: "I got up __ than my mother",
        rightAnswerId: 2,
        id: 3,
        answers: [
          { text: "the earliest", id: 1 },
          { text: "earlier", id: 2 },
          { text: "earliest", id: 3 },
          { text: "early", id: 4 }
        ]
      },
      {
        question: 'How do you spell "dog" ?',
        rightAnswerId: 3,
        id: 4,
        answers: [
          { text: "Cat", id: 1 },
          { text: "No, i don't", id: 2 },
          { text: "D-O-G", id: 3 },
          { text: "I have one dog", id: 4 }
        ]
      },
      {
        question: "Where do you usually eat lunch?",
        rightAnswerId: 1,
        id: 5,
        answers: [
          { text: "In the cafeteria", id: 1 },
          { text: "With Jane", id: 2 },
          { text: "Sandwich", id: 3 },
          { text: "At 12:00", id: 4 }
        ]
      },
      {
        question: "What do you like to drink?",
        rightAnswerId: 4,
        id: 6,
        answers: [
          { text: "Two ", id: 1 },
          { text: "Saturday evening", id: 2 },
          { text: "With my friends", id: 3 },
          { text: "Coffee", id: 4 }
        ]
      },
      {
        question: "Who do you usually go shopping with?",
        rightAnswerId: 1,
        id: 7,
        answers: [
          { text: "By myself", id: 1 },
          { text: "Downtown", id: 2 },
          { text: "On Sunday", id: 3 },
          { text: "Two or three hours", id: 4 }
        ]
      },
      {
        question: "What is your busiest day of the week?",
        rightAnswerId: 1,
        id: 8,
        answers: [
          { text: "Tuesday", id: 1 },
          { text: "Every day", id: 2 },
          { text: "In the morning", id: 3 },
          { text: "Last week", id: 4 }
        ]
      },
      {
        question: "How long did you study last night?",
        rightAnswerId: 3,
        id: 9,
        answers: [
          { text: "With Bob", id: 1 },
          { text: "In my room", id: 2 },
          { text: "For three hours", id: 3 },
          { text: "English", id: 4 }
        ]
      },
      {
        question: "Will you __ your car to school tomorrow?",
        rightAnswerId: 3,
        id: 10,
        answers: [
          { text: "driving", id: 1 },
          { text: "drives", id: 2 },
          { text: "drive", id: 3 },
          { text: "drove", id: 4 }
        ]
      }
    ]
  };

  onAnswerClickHandler = answerId => {
    if (this.state.answerState) {
      const key = Object.keys(this.state.answerState)[0];
      if (this.state.answerState[key] === "success") {
        return;
      }
    }

    const question = this.state.quiz[this.state.activeQuestion];
    const results = this.state.results;

    if (question.rightAnswerId === answerId) {
      if (!results[question.id]) {
        results[question.id] = "success";
      }

      this.setState({
        answerState: { [answerId]: "success" },
        results
      });

      const timeout = window.setTimeout(() => {
        if (this.isQuizFinished()) {
          this.setState({
            isFinished: true
          });
        } else {
          this.setState({
            activeQuestion: this.state.activeQuestion + 1,
            answerState: null
          });
        }
        window.clearTimeout(timeout);
      }, 1000);
    } else {
      results[question.id] = "error";
      this.setState({
        answerState: { [answerId]: "error" },
        results
      });
    }
  };

  isQuizFinished() {
    return this.state.activeQuestion + 1 === this.state.quiz.length;
  }

  retryHandler = () => {
    this.setState({
      activeQuestion: 0,
      answerState: null,
      isFinished: false,
      results: {}
    });
  };

  render() {
    return (
      <div className={classes.Quiz}>
        <div className={classes.QuizWrapper}>
          <h1>Ответьте на все вопросы</h1>

          {this.state.isFinished ? (
            <FinishedQuiz
              results={this.state.results}
              quiz={this.state.quiz}
              onRetry={this.retryHandler}
            />
          ) : (
            <ActiveQuiz
              answers={this.state.quiz[this.state.activeQuestion].answers}
              question={this.state.quiz[this.state.activeQuestion].question}
              onAnswerClick={this.onAnswerClickHandler}
              quizLength={this.state.quiz.length}
              answerNumber={this.state.activeQuestion + 1}
              state={this.state.answerState}
            />
          )}
        </div>
      </div>
    );
  }
}

export default Quiz;
